let timer = '';
const app = getApp();
export default{
	data(){
		return{
			imag: app.globalData.imag,
			timers:3,
			current:1,
			price:0,
			num:'',
			num2:''
		}
	},
	onLoad(option) {
		this.current = option.current
		this.price = option.price
		this.num = option.num
		this.getNum()
		this.countdown()
	},
	methods:{
		tomymoney(){
			clearInterval(timer);
			uni.redirectTo({
				url: '/pages/viper/mymoney'
			})
		},
		getNum(){
			if(this.num.length >= 4){
				this.num2 = this.num.substring(this.num.length - 4)
			}else if(this.num.length == 3){
				this.num2 = '*'+ this.num
			}else if(this.num.length == 2){
				this.num2 = '**'+ this.num
			}else if(this.num.length == 1){
				this.num2 = '***'+ this.num
			}else{
				this.num2 = '****'
			}
		},
		countdown(){
			clearInterval(timer);
			timer = setInterval(() => {
				this.timers--;
				if (this.timers <= 0) {
					uni.redirectTo({
						url: '/pages/viper/mymoney'
					})
					clearInterval(timer);
				}
			}, 1000)
		}
	}
}

